import React from "react";
import { ReactSVG } from "react-svg";
import "./Infrastructure.scss";
import office from "./assets/icons/office.svg";
import database from "./assets/icons/database-server.svg";
import tower from "./assets/icons/electric-tower.svg";
import { createElement } from "react";

function CreatePath(coordinates) {
  return createElement(
    "svg",
    {
      className: "path",
      viewBox: "0 0 1000 1000",
    },
    createElement("path", {
      fill: "none",
      stroke: "lightgray",
      strokeWidth: "10",
      // d: coordinates,
      d: "M140,384 C200,380 700,180 840,180",
    })
  );
}

export default class Infrastructure extends React.Component {
  constructor(props) {
    super(props);
    this.state = { path: null };
  }

  containerCoordinates = (id) => {
    const container = document.getElementById(id);
    if (!container) {
      console.log("No such Container!");
      return;
    }
    const coords = container.getBoundingClientRect();
    console.log(container.getBoundingClientRect());
    const x = coords.x + coords.width;
    const y = coords.y + coords.height / 2;
    console.log("Returning ", id, { x, y });
    return { x, y };
  };

  createBezierCoords = (start, end) => {
    return (
      "M" + start.x + "," + start.y + " C" + "15,5 100,5 " + end.x + "," + end.y
    );
  };

  componentDidMount() {
    const path = CreatePath(
      this.createBezierCoords(
        this.containerCoordinates("database"),
        this.containerCoordinates("tower")
      )
    );
    console.log("path ", path);
    this.setState({
      path,
    });
  }

  render() {
    return (
      <main className="infrastructure infrastructure-wrapper">
        {this.state.path}
        <section className="column">
          <article className="container">
            <ReactSVG className="svg-wrapper" src={office} />
          </article>
          {/* <svg viewBox="0 0 1000 500">
            <path
              id="path"
              fill="none"
              stroke="#333"
              strokeWidth="3"
              d="M10,55 C15,5 100,5 100,5"
            />
          </svg> */}
          <article id="database" className="container">
            <ReactSVG className="svg-wrapper" src={database} />
          </article>
        </section>

        {/* <section className="column">
          <article className="container">
            <ReactSVG className="svg-wrapper" src={arrowLeft} />
          </article>
        </section> */}
        <section className="column">
          <article id="tower" className="container">
            <ReactSVG className="svg-wrapper" src={tower} />
          </article>
        </section>
      </main>
    );
  }
}
